import pdfUrl from './AGR010  Pantallas térmicas en invernaderos.pdf';

import React, { useEffect, useState } from "react";
import * as pdfjsLib from "pdfjs-dist";
import { PDFDocument } from "pdf-lib";
import { useAuthState } from "react-firebase-hooks/auth";
import { auth } from "../../components/firebase/firebaseConfig";
import {
  IconButton,
  Typography,
  List,
  ListItem,
  ListItemText,
  Link,
  CircularProgress
} from "@mui/material";
import { toast, ToastContainer } from "react-toastify";

import {
  Tune as TuneIcon,
  Download as DownloadIcon
} from "@mui/icons-material";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router-dom";

import config from '../../components/configURL';
// Configuración de pdf.worker
pdfjsLib.GlobalWorkerOptions.workerSrc = `/workers/3.11.174/pdf.worker.min.js`;
const ENDPOINT = "numero"
const PDF_API_URL = `${config.API_URL}/${ENDPOINT}?timestamp=${new Date().getTime()}`;



const PDFRenderer = () => {
  const [user] = useAuthState(auth);
  const navigate = useNavigate();
  const [images, setImages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [formData, setFormData] = useState({});

  const calculados = (data) => ({
    "S": "44",
    "Ki": "44",
    "Kp": "22",
  });

  const fetchPdf = async (pdfUrl) => {
    try {
      return await fetch(pdfUrl).then((res) => res.arrayBuffer());
    } catch (err) {
      console.error("Error fetching PDF:", err);
      throw new Error("Failed to fetch PDF.");
    }
  };

  const fillPdfFields = async (pdfBytes) => {
    try {
      const pdfDoc = await PDFDocument.load(pdfBytes);
      const form = pdfDoc.getForm();
      const updatedData = calculados(formData);

      Object.entries(updatedData).forEach(([name, value]) => {
        try {
          const field = form.getField(name);
          field.setText(String(value));
        } catch (err) {
          console.warn(`Field '${name}' not found in PDF:`, err.message);
        }
      });

      return await pdfDoc.save();
    } catch (err) {
      console.error("Error filling PDF fields:", err);
      throw new Error("Failed to fill PDF fields.");
    }
  };

  const renderPdfAsImages = async (pdfBytes) => {
    try {
      const pdf = await pdfjsLib.getDocument({ data: pdfBytes }).promise;
      const pages = [];

      for (let i = 1; i <= pdf.numPages; i++) {
        const page = await pdf.getPage(i);
        const viewport = page.getViewport({ scale: 2.5 });
        const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d");
        canvas.width = viewport.width;
        canvas.height = viewport.height;
        await page.render({ canvasContext: context, viewport }).promise;
        pages.push(canvas.toDataURL("image/png"));
      }

      setImages(pages);
    } catch (err) {
      console.error("Error rendering PDF as images:", err);
      setError("Failed to render PDF.");
    }
  };



  const handleDownload = async () => {
    if (!user) return toast.error("Debe iniciar sesión");

    try {
      const pdfBytes = await fetchPdf(pdfUrl);
      const modifiedPdfBytes = await fillPdfFields(pdfBytes);
      const blob = new Blob([modifiedPdfBytes], { type: "application/pdf" });
      const link = document.createElement("a");
      link.href = URL.createObjectURL(blob);
      link.download = "modified-pdf.pdf";
      link.click();
    } catch (err) {
      console.error("Error downloading PDF:", err);
    }
  };

  useEffect(() => {
    const initializePdf = async () => {
      try {
        setLoading(true);
        const response = await fetch(pdfUrl);
        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
        const pdfBytes = await response.arrayBuffer();
        const modifiedPdfBytes = await fillPdfFields(pdfBytes);
        await renderPdfAsImages(modifiedPdfBytes);
      } catch (err) {
        console.error("Initialization error:", err);
        setError("No se pudo inicializar el PDF.");
      } finally {
        setLoading(false);
      }
    };
    initializePdf();
  }, []);

  
  return (
    <div>
      <IconButton
        onClick={() => toast.info("Configuración en desarrollo")}
        color="success"
        style={{
          position: 'fixed', // Fija el botón en la pantalla
          top: '50%', // Distancia desde el borde inferior
          right: '20px', // Distancia desde el borde derecho
          backgroundColor: 'white', // Color de fondo para mayor visibilidad
          boxShadow: '0px 4px 6px rgba(0,0,0,0.1)', // Sombra para destacar el botón
          backgroundColor: "rgba(255, 255, 255, 0.5)", // Blanco translúcido

        }}      >
        <TuneIcon />
      </IconButton>

      {user && (
        <IconButton
          onClick={handleDownload}
          color="success"
          style={{
            position: 'fixed', // Fija el botón en la pantalla
            top: '60%', // Distancia desde el borde inferior
            right: '20px', // Distancia desde el borde derecho
            backgroundColor: 'white', // Color de fondo para mayor visibilidad
            boxShadow: '0px 4px 6px rgba(0,0,0,0.1)', // Sombra para destacar el botón
            backgroundColor: "rgba(255, 255, 255, 0.5)", // Blanco translúcido

          }}        >
          <DownloadIcon />
        </IconButton>
      )}

      {loading && <CircularProgress style={loadingStyle} />}
      {error && <Typography color="error">{error}</Typography>}

      <div>
        {images.map((src, index) => (
          <img
            key={index}
            src={src}
            alt={`Page ${index + 1}`}
            style={imageStyle}
          />
        ))}
      </div>
      <ToastContainer />
    </div>
  );
};



const loadingStyle = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
};

const imageStyle = {
  width: "100%",
  borderRadius: "8px",
};

export default PDFRenderer;
